<template>
  <div class="params">
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>参数列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
      <!-- 同步警告区域 -->
      <el-alert title="注意：只允许为第三级分类设置相关参数！" type="warning" :closable="false" show-icon></el-alert>
      <!-- 选择商品分类区域 -->
      <el-row class="cat_opt">
        <el-col>
          <span>选择商品分类：</span>
          <!--级联选择框 -->
          <el-cascader
            v-model="seletKeys"
            expand-trigger="hover"
            :options="paramsList"
            @change="paramsChange"
            :props="addProps"
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- tab页签 -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick" class="tabl">
        <!-- 添加动态参数的面板 -->
        <el-tab-pane label="动态参数" name="first">
          <el-button type="primary" size="mini">添加参数</el-button>
        </el-tab-pane>
        <!-- 添加静态属性的面板 -->
        <el-tab-pane label="静态属性" name="second">
          <el-button type="primary" size="mini">添加属性</el-button>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 商品分类数据
      paramsList: [],
      //props对象
      addProps: {
        value: "cat_id",
        lable: "cat_name",
        children: "children"
      },
      //级联选择框双向绑定的数组
      seletKeys: [],
      // 被激活的页签的名称
      activeName: "first"
    };
  },
  created() {
    // 调用商品分类数据
    this.getParamsList();
  },
  methods: {
    // 获取商品分类的数据
    async getParamsList() {
      let { data: res } = await this.$http.get("categories");
      if (res.meta.status !== 200) {
        this.$message.error("获取商品分类数据失败！");
      } else {
        this.paramsList = res.data;
        // console.log(this.paramsList)
      }
    },
    // 级联选择选中项变化，会触发这个函数
    paramsChange() {
      // console.log(this.seletKeys);
    },
    // 页签tab的切换事件
    handleTabClick() {
      console.log(this.activeName);
    },
    // 如果按钮需要被禁用，则返回true,否则返回false
    isTabDisabled(){
      if(this.seletKeys.length !== 3){
        return true
      }
        return false
      
    }
  }
};
</script>

<style scoped>
.cat_opt {
  margin-top: 15px;
}
.el-cascader-menu__list {
  height: 200px;
  font-size: 14px;
}
.el-cascader {
  width: 20%;
}
.tabl{
  margin-top: 15px;
}
</style>