<template>
  <div class="categories">
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showaddCartDialog">添加分类</el-button>
        </el-col>
      </el-row>
      <!-- 表格 -->
      <tree-table
        :data="cateList"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
        :show-row-hover="false"
        class="TreeTab"
      >
        <!-- 是否有效 -->
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-success"
            v-if="scope.row.cat_deleted === false"
            style="color:lightgreen;"
          ></i>
          <i class="el-icon-error" v-else style="color:red;"></i>
        </template>
        <!-- 排序 -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0">一级</el-tag>
          <el-tag type="success" size="mini" v-else-if="scope.row.cat_level === 1">二级</el-tag>
          <el-tag type="warning" size="mini" v-else>三级</el-tag>
        </template>
        <!-- 操作 -->
        <template slot="opt">
          <el-button type="primary" icon="el-icon-edit" size="mini">编辑</el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini">删除</el-button>
        </template>
      </tree-table>
      <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <!-- 添加分类对话框 -->
      <el-dialog title="添加分类" :visible.sync="categoriesVisible" width="50%" @close="addDelete">
        <!-- 添加分类表单 -->
        <el-form
          :model="addCartForm"
          :rules="addCartFormRules"
          ref="addCartFormRef"
          label-width="100px"
        >
          <el-form-item label="分类名称" prop="cat_name">
            <el-input v-model="addCartForm.cat_name"></el-input>
          </el-form-item>
          <el-form-item label="父级分类">
            <!-- options用来指定数据源 -->
            <el-cascader
              v-model="selectedKeys"
              :options="parentCartList"
              expand-trigger="hover"
              :props="cascad"
              @change="adChange"
              clearable
            ></el-cascader>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="categoriesVisible = false">取 消</el-button>
          <el-button type="primary" @click="addCart">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 商品分类的数据列表，默认为空
      cateList: [],
      // 总条数
      total: 0,
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      columns: [
        {
          label: "分类名称",
          prop: "cat_name"
        },
        {
          label: "是否有效",
          type: "template",
          template: "isok"
        },
        {
          label: "排序",
          type: "template",
          template: "order"
        },
        {
          label: "操作",
          type: "template",
          template: "opt"
        }
      ],
      categoriesVisible: false,
      // 添加分类的表单对象
      addCartForm: {
        // 将要添加的分类名称
        cat_name: "",
        // 父级分类id
        cat_pid: 0,
        // 分类的等级，默认要添加的是以及分类
        cat_level: 0
      },
      // 添加分类表单验证规则对象
      addCartFormRules: {
        cat_name: [
          { required: true, message: "请输入分类的名称", trigger: "blur" }
        ]
      },
      //父级分类的列表
      parentCartList: [],
      //指定级联选择器的配置对象
      cascad: {
        value: "cat_id",
        label: "cat_name",
        children: "children"
      },
      //选中的父级id的数组
      selectedKeys: []
    };
  },
  created() {
    // 调用商品分类数据
    this.getCateList();
  },
  methods: {
    //商品分类数据
    async getCateList() {
      let { data: res } = await this.$http.get("categories", {
        params: this.queryInfo
      });
      if (res.meta.status !== 200) {
        this.$message.error("获取商品分类数据失败！");
      } else {
        // console.log(res)
        this.cateList = res.data.result;
        // 为总条数赋值
        this.total = res.data.total;
      }
    },
    // 监听pagesize改变
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      // 调用商品分类数据
      this.getCateList();
    },
    // 监听pagenum改变
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      // 调用商品分类数据
      this.getCateList();
    },
    //添加分类按钮
    showaddCartDialog() {
      // 先获取父级分类列表的数据
      this.getPanentCateList();
      this.categoriesVisible = true;
    },
    // 获取父级分类的数据列表
    async getPanentCateList() {
      let { data: res } = await this.$http.get("categories", {
        params: { type: 2 }
      });
      if (res.meta.status !== 200) {
        this.$message.error("获取父级分类数据失败！");
      } else {
        //  console.log(res.data)
        this.parentCartList = res.data;
      }
    },
    // 选择项发生变化触发这个函数
    adChange() {
      if (this.selectedKeys.length > 0) {
        //父级id
        this.addCartForm.cat_pid = this.selectedKeys[
          this.selectedKeys.length - 1
        ];
        //  为当前的等级分类赋值
        this.addCartForm.cat_level = this.selectedKeys.length;
        return;
      } else {
        this.addCartForm.cat_pid = 0;
        this.addCartForm.cat_level = 0;
      }
    },
    // 点击按钮，添加新的分类
    addCart() {
      this.$refs.addCartFormRef.validate(async valid => {
        if (!valid) return;
        let { data: res } = await this.$http.post(
          "categories",
          this.addCartForm
        );
        if (res.meta.status !== 201) {
          this.$message.error("添加分类失败！");
        } else {
          this.$message.success("添加分类成功");
          // 调用商品分类数据
          this.getCateList();
          this.categoriesVisible = false;
        }
      });
    },
    // 监听对话框关闭事件，重置表单数据
    addDelete() {
      this.$refs.addCartFormRef.resetFields();
      this.selectedKeys = [];
      this.addCartForm.cat_pid = 0;
      this.addCartForm.cat_level = 0;
    }
  }
};
</script>

<style lang="less">
.TreeTab {
  margin-top: 15px;
}
.el-cascader {
  width: 100%;
}
.el-cascader-menu__list {
  height: 200px;
  font-size: 14px;
}
</style>